<template>
  <div v-if="createCanvas" class="rainbow-waves">
    <canvas :id="elId"></canvas>
  </div>
</template>

<script>
export default {
  name: "rainbow-waves",
  props: {
    config: {
      type: Object,
    },
    waves: {
      type: Array,
    },
  },
  computed: {
    createCanvas() {
      return this.config ? this.config.new || true : true;
    },
    elId() {
      return this.config ? this.config.el || "rainbow-waves" : "rainbow-waves";
    },
  },
  watch: {
    config: {
      handler() {
        this.drow();
      },
      deep: true,
    },
    waves: {
      handler() {
        this.drow();
      },
      deep: true,
    },
  },
  mounted() {
    this.drow();
  },
  methods: {
    drow() {
      // 
      let t = 0;
      let waves = [];
      let canvas = null;
      let ctx = null;
      let width = 0;
      let height = 0;

      // 
      const {
        new: create,
        el,
        width: xW,
        height: xH,
        clear,
        background,
        direction,
      } = {
        el: "rainbow-waves",
        clear: true,
        new: true,
        width: 1920,
        height: 1000,
        background: {
          type: "color",
          color: "#fff",
        },
        direction: "bottom",
        ...this.config,
      };

      //  
      canvas = document.getElementById(el);
      // 
      if (!canvas) throw new Error(`ID ${el} `);
      if (!canvas) return;
      ctx = canvas.getContext("2d");
      //  
      width = xW;
      height = xH;
      if (create) {
        canvas.width = width;
        canvas.height = height;
        setColor(background);
        ctx.fillRect(0, 0, width, height);
      }
      // 
      function setColor(background) {
        // 
        if (!ctx) throw new Error(`canvas, getContext `);
        if (!ctx) return;
        switch (background.type) {
          case "color":
            ctx.fillStyle = background.color;
            break;
          case "gradient":
            const [o1, o2, o3, o4] = background.position;
            const gradient = ctx.createLinearGradient(o1, o2, o3, o4);
            const l = background.color.length;
            background.color.forEach((x, k) => {
              gradient.addColorStop(k / l, x);
            });
            ctx.fillStyle = gradient;
            break;
          case "image":
            let img = new Image();
            img.src = background.src;
            ctx.fillStyle = ctx.createPattern(img, background.repetition);
            break;
        }
      }

      // 
      if (this.waves && this.waves.length) {
        waves = [...this.waves];
        loop();
        function loop() {
          if (clear) ctx.clearRect(0, 0, width, height);
          if (create) setColor(background);
          ctx.fillRect(0, 0, width, height);
          for (let i = 0; i < waves.length; i++) {
            let { jitter, restore, waveGap, waterGap, waveUps } = waves[i];
            ctx.beginPath();
            for (let e = 0; e <= 1 + 0.01; e += 0.01) {
              if (direction === "left" || direction === "right") {
                ctx.lineTo(
                  width *
                    (direction === "left"
                      ? waves[i]["bit"]
                      : Math.floor((1 - waves[i]["bit"]) * 10) / 10) +
                    (Math.sin(e * waveUps + t * jitter) * waveGap + waterGap) *
                      Math.sin(t * restore),
                  e * height
                );
              } else {
                ctx.lineTo(
                  e * width,
                  height *
                    (direction === "top"
                      ? waves[i]["bit"]
                      : Math.floor((1 - waves[i]["bit"]) * 10) / 10) +
                    (Math.sin(e * waveUps + t * jitter) * waveGap + waterGap) *
                      Math.sin(t * restore)
                );
              }
            }

            switch (direction) {
              case "left":
                ctx.lineTo(0, height);
                ctx.lineTo(0, 0);
                break;
              case "right":
                ctx.lineTo(width, height);
                ctx.lineTo(width, 0);
                break;
              case "top":
                ctx.lineTo(width, 0);
                ctx.lineTo(0, 0);
                break;
              default:
                ctx.lineTo(width, height);
                ctx.lineTo(0, height);
                break;
            }
            ctx.closePath();
            setColor(waves[i]["background"]);
            ctx.fill();
          }
          t++;
          requestAnimationFrame(loop);
        }
      }
    },
  },
};
</script>